import { FC, forwardRef } from 'react';
import {
  TextInput as MantineTextInput,
  TextInputProps as MantineTextInputProps,
  MantineTheme,
} from '@mantine/core';
import { EventHandler, TwBaseProps, TWOnChange } from '../../types';
import { styled } from '../../utils';
import { wrapInputOnChange } from '../../utils/inputElementEvents';

export interface TextInputProps
  extends TwBaseProps,
    Omit<EventHandler<React.InputHTMLAttributes<HTMLInputElement>>, 'onChange'>,
    Pick<
      MantineTextInputProps,
      | 'value'
      | 'label'
      | 'placeholder'
      | 'description'
      | 'type'
      | 'size'
      | 'error'
      | 'disabled'
      | 'radius'
      | 'required'
    > {
  leadingIcon?: React.ReactNode;
  trailingIcon?: React.ReactNode;
  onChange?: TWOnChange<string>;
}

const Root = styled(MantineTextInput)<MantineTextInputProps>(() => ({}));

// TODO: Forwarding ref doesn't work here
export const TextInput: FC<TextInputProps> = forwardRef((props, ref) => {
  const { leadingIcon, trailingIcon, radius = 'xs', ...other } = props;
  return (
    <Root
      {...other}
      icon={leadingIcon}
      rightSection={trailingIcon}
      inputWrapperOrder={['label', 'input', 'description', 'error']}
      radius={radius}
      styles={(theme: MantineTheme) => {
        return {
          input: {
            borderColor: theme.colors.gray[3],
            minHeight: '38px', // TODO: Keep this here?  Only did this to match Traffic Rules page, but maybe it's best to completely change
            ...(!!props.error && {
              position: 'relative',
              top: theme.spacing.xs,
            }),
          },
          error: {
            position: 'relative',
            top: theme.spacing.xs,
            paddingTop: 5,
          },
        };
      }}
      ref={ref}
      onChange={wrapInputOnChange(props?.onChange)}
    />
  );
});
